    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Montserrat", sans-serif;
    text-align: center;
}

body {
    background: #000000;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

#content {
    width: 100vw;
    height: 100vh;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
}

#main {
    height: 100%;
    max-height: calc(100% - 188px);
    overflow: scroll;
}

.cursive {
    font-family: "Herr Von Muellerhoff", cursive;
    font-size: 96px;
    text-align: center;
    text-transform: capitalize;
}

h1 {
    font-size: 64px;
    font-weight: bold;
    margin-top: -72px;
    text-align: center;
    text-transform: uppercase;
}

h2 {
    margin: 8px;
}

p {
    line-height: 150%;
}

hr {
    width: 280px;
    border: 2px solid #c59d5f;
    margin: 24px auto;
}

.white {
    color: white;
}

.golden {
    color: #c59d5f;
}

.gray {
    color: #262626;
}

.bg-white {
    background: white;
